export default {
	name: 'uniLoadMore',
	data() {
		return {
			productList: []
		}},
	created() {
		this.changData()
	},
	methods: {
		// 拿数据
		async changData() {
			let [error, res] = await uni.request({
				url: 'http://ceshi3.dishait.cn/api/index_category/data'
			})
			this.productList=res.data.data.data[4].data
			console.log(res.data.data.data[4].data)
		}
	}
};
